<template>
  <main v-if="project?.card" class="container mx-auto px-4 py-12 flex-1">
    <h2 class="text-3xl font-bold mb-6 text-center">{{ project.card.title }}</h2>
    <div class="bg-gray-800 p-6 rounded-lg">
      <p class="mb-4">{{ project.card.description }}</p>
      <div class="space-y-2">
        <p v-if="project.card.gitURL"><strong>{{ project.card.github }}:</strong> <a :href="project.card.gitURL" target="_blank" class="text-blue-400 hover:underline"> {{ project.card.gitlink }}</a></p>
        <p v-if="project.card.gameLink && project.card.gameName"><strong>{{ project.card.play }}:</strong> <a :href="project.card.gameLink" target="_blank" class="text-blue-400 hover:underline">{{ project.card.gameName }}</a></p>
        <p><strong>{{ project.card.tools }}:</strong> <span>{{ project.card.skills }}</span></p>
        <p><strong>{{ project.card.details }}:</strong><br/> <span class="whitespace-pre-line">{{ project.card.detailsText }}</span></p>
      </div>
      <h3 class="text-xl font-semibold mt-6">{{ project.card.screenshots }}</h3>
      <viewer class="grid grid-cols-1 md:grid-cols-3 gap-4 mt-4" :images="project.card.images">
        <img v-for="src in project.card.images" :key="src" :src="src" class="rounded-lg" alt="">
      </viewer>
    </div>
  </main>
</template>

<script>
export default {
  name: "ProjectView",
  inject: ['getLang', 'getTranslations'],
  computed: {
    lang() {
      return this.getLang();
    },
    translations() {
      return this.getTranslations();
    },
    project() {
      const id = Number(this.$route.params.id);
      return this.translations.projects.list.find(p => p.id === id);
    }
  },
}
</script>